import {registerLocale, setDefaultLocale} from "react-datepicker";
import ru from "date-fns/locale/ru";

registerLocale('ru', ru)

export default function buildLocalizeFn(args) {
    return function (dirtyIndex, dirtyOptions) {
        var options = dirtyOptions || {}

        var context = options.context ? String(options.context) : 'standalone'

        var valuesArray
        if (context === 'formatting' && args.formattingValues) {
            const defaultWidth = args.defaultFormattingWidth || args.defaultWidth
            const width = options.width ? String(options.width) : defaultWidth
            valuesArray =
                args.formattingValues[width] || args.formattingValues[defaultWidth]
        } else {
            const defaultWidth = args.defaultWidth
            const width = options.width ? String(options.width) : args.defaultWidth
            valuesArray = args.values[width] || args.values[defaultWidth]
        }
        var index = args.argumentCallback
            ? args.argumentCallback(dirtyIndex)
            : dirtyIndex
        return valuesArray[index]
    }
}

const monthValues = {
    narrow: ['', '', '', '', '', '', '', '', '', '', '', ''],
    abbreviated: ['.', '.', '', '.', '', '', '', '.', '.', '.', '.', '.'],
    wide: ['', '', '', '', '', '', '', '', '', '', '', '']
};

const formattingMonthValues = {
    narrow: ['', '', '', '', '', '', '', '', '', '', '', ''],
    abbreviated: ['.', '.', '.', '.', '', '.', '.', '.', '.', '.', '.', '.'],
    wide: ['', '', '', '', '', '', '', '', '', '', '', '']
};

ru.localize.month = buildLocalizeFn({
    values: monthValues,
    defaultWidth: 'wide',
    formattingValues: formattingMonthValues,
    defaultFormattingWidth: 'wide'
})

setDefaultLocale("ru")

export const ru_RU = ru

export const getStatusName = (status: String) => {
    return [
        {v: 'ALL', c: ''},
        {v: 'PENDING', c: ''},
        {v: 'BOOKED', c: ''},
        {v: 'DECLINED', c: ''},
    ].filter(s => s.v === status)
        .map(s => s.c)
        .pop()

}

export const getTimeUnitName = (status: String) => {
    return [
        {v: 'HOUR', c: ''},
        {v: 'DAY', c: ''},
        {v: 'MONTH', c: ''},
        {v: 'YEAR', c: ''},
    ].filter(s => s.v === status)
        .map(s => s.c)
        .pop()

}

export const getAssetTypeName = (status: String) => {
    return [
        {label: " ", id: "WORK_PLACE"},
        {label: " ", id: "OFFICE"},
        {label: "", id: "MEETING_ROOM"},
        {label: "  ", id: "EVENT_PLACE"},
        {label: "", id: "OTHER"},
    ].filter(s => s.id === status)
        .map(s => s.label)
        .pop()

}



